<template>
  <dashboard-barber-card>
    <h1 class="titulo">
      Total:
    </h1>
    <span class="valor mt-md-4 d-flex align-center">
      <v-icon>monetization_on</v-icon>
      <span class="success--text ml-2">
        R$ {{ valueModel }}
      </span>
    </span>
  </dashboard-barber-card>
</template>

<script>
import DashboardBarberCard from './DashboardBarberCard'
import TextMixin from '@/mixins/TextMixin'
export default {
  name: 'DashboardBarberRevenues',
  components: { DashboardBarberCard },
  mixins: [ TextMixin ],
  props: {
    value: { default: '0.00' }
  },
  computed: {
    valueModel () {
      return this.setMoneyMask(this.value)
    }
  }
}
</script>

<style scoped>
.titulo { color: #919191; font-size: 1rem; }
.valor { font-size: 1.5rem; color: #4F4F4F; font-weight: bold; }
</style>